/* App.css */

body {
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #f0f0f0;
  background-image: url('背景.png');
  
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center; /* 水平居中 */
  justify-content: flex-start; /* 垂直居中 */
  margin: 0 auto; /* 水平居中 */
  max-width: 5000px; 
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.board-row {
  display: flex;
}

.square {
  width: 30px;
  height: 30px;
  margin: 2px;
  border: 1px solid #999;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
}

.status {
  margin-top: 30px;
}

.game-title {
  text-align: center;
  margin-bottom: 10px;
  color: #565151;
  width: 100%;
  font-size: 28px;
}


.game-info {
  margin-top: 25px;
  max-height:  500px; ;
  max-width: 500px;
  margin-left: 50px;
  width: 40%;
  
  overflow: auto;

}

.game-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-left: 100px;
  margin-bottom: 50px;
}

button {
  padding: 5px 10px;
  cursor: pointer;
  border: #3e3c3c 0.5px solid;
  font-size: 15px;
  border-radius: 2px;
  margin-right: 20px;
  margin-top: 5px;
  border-radius: 3px;
  background-color: rgba(255, 255, 255, 0.3);
}
.function-button {
  margin-top: 25px;
  
}